@use 'global'as *;

.p-header {
  height: rem(40);
  @include mq(md) {
    height: rem(80);
  }
}

.p-header__inner {
  display: flex;
  align-items: center;
  height: inherit;
  padding-left: rem(20);
  @include mq(md) {
    padding-left: rem(40);
  }
}

.p-header__logo {
  max-width: rem(100);
  width: 100%;
  @include mq(md) {
    max-width: rem(125);
    width: 100%;
  }
}