@use 'global'as *;

.p-contact__inner {
  padding-top: rem(40);
  padding-bottom: rem(100);
  @include mq(md) {
  padding-top: 0;
  }
}

.p-contact__lead {
  font-size: rem(16);
  font-weight: bold;
  line-height: 1.75;
  text-align: center;
  color: $colorGray;
  @include mq(md) {
    font-size: rem(20);
  }
}

.p-contact__form {
  margin-top: rem(60);
  @include mq(md) {
    margin-top: rem(76);
  }
}

