@use 'global'as *;

.p-lower-nav__lists {
  @include mq(md) {
    display: flex;
    align-items: center;
  }
}

.p-lower-nav__list {
  @include mq(md) {
    display: flex;
    align-items: center;
    font-size: rem(16);
    font-weight: bold;
    text-transform: uppercase;
    color: $colorWhite;
  }
}

.p-lower-nav__list--contact a {
  @include mq(md) {
    display: flex;
    align-items: center;
    background: $colorRed;
    padding: 0 rem(46);
    height: inherit;
  }
}

.p-lower-nav__list--contact a {
  display: block;
}

.p-lower-nav__list + .p-lower-nav__list {
  @include mq(md) {
    margin-left: rem(50);
  }
}

.p-lower-nav__list a {
  display: block;
  padding:rem(10) 0;
}