@use 'global'as *;

.p-card__title {
  margin-top: rem(20);
  font-size: rem(14);
  font-weight: bold;
}

.p-card__title span {
  font-weight: normal;
}

.p-card__text {
  margin-top: rem(10);
  font-size: rem(14);
}

.p-cards__item + .p-cards__item {
  margin-top: rem(44);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-card--pc {
  display: none;
  @include mq(md) {
    display: block;
  }
}