@use 'global'as *;

.c-buger-button {
  /* ボタンの大きさ  */
  position: relative;
  width: rem(40);
  height: rem(40);
  margin-left: auto;
  padding: 5px;
  cursor: pointer;
  /* 最前面に */
  z-index: 10;
  background: $colorBlack;
  @include mq(md) {
    width: rem(80);
    height: rem(80);
  }
}

.c-buger-button.open {
  position: fixed;
  top: 0;
  right: 0;
}

.c-buger-button--lower {
  @include mq(md) {
    display: none;
  }
}

.c-buger-button__line {
	display: block;
	position: relative;  /* バーガー線の位置基準として設定 */
	width: rem(20);  /* 線の長さと高さ */
	height: 1px;
  margin: 0 auto;
	background-color: $colorWhite;  /* バーガー線の色 */
	transition: all .3s;
  @include mq(md) {
    width: rem(40);
    height: rem(4);
  }
}

.c-buger-button__line::before,
.c-buger-button__line::after {
  content: "";
  position: absolute;
  width: rem(20);
  height: 100%;
  background-color: $colorWhite;
  left: 0;
  transition: all .3s;
  @include mq(md) {
    width: rem(40);
  }
}
.c-buger-button__line::before {
  /* 上の線の位置 */
  transform: translateY(rem(-7.5));
  transition: all .3s;
  @include mq(md) {
    transform: translateY(rem(-15));
  }
}
.c-buger-button__line::after {
  /* 下の線の位置 */
  transform: translateY(rem(7.5));
  transition: all .3s;
  @include mq(md) {
    transform: translateY(rem(15));
  }
}

/***** メニューオープン時 *****/
.c-buger-button__line.open {
	background-color: transparent;  	/* 真ん中の線を透明に */
  transition: all .3s;
}
.c-buger-button__line.open::before ,
.c-buger-button__line.open::after {
	content: "";
	background-color: $colorWhite;  /* 上下の線の色を変える */
}
.c-buger-button__line.open::before {
	transform: rotate(45deg);  /* 上の線を傾ける */
  transition: all .3s;
}
.c-buger-button__line.open::after {
	transform: rotate(-45deg);  /* 下の線を傾ける */
  transition: all .3s;
}