@use 'global'as *;

.c-mv-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: rem(30);
  font-weight: bold;
  color: $colorWhite;
  text-transform: uppercase;
  @include mq(md) {
    font-size: rem(44);
    left: 9.6%;
    transform: translate(0, -50%);
  }
}