@use 'global'as *;

.p-lower-header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: rem(40);
  z-index: 3;
  @include mq(md) {
    height: rem(80);
  }
}

.p-lower-header__logo {
  max-width: rem(100);
  width: 100%;
  @include mq(md) {
    max-width: rem(125);
    width: 100%;
  }
}

.p-lower-header__inner {
  display: flex;
  align-items: center;
  height: inherit;
  padding-left: rem(20);
  @include mq(md) {
    padding-left: rem(40);
  }
}

.p-lower-header__nav-pc,
.p-lower-header__nav-pc nav,
.p-lower-header__nav-pc .p-lower-nav__lists,
.p-lower-header__nav-pc .p-lower-nav__list {
  @include mq(md) {
    height: inherit;
  }
}

.p-lower-header__nav-sp {
  @include mq(md) {
    display: none;
  }
}

.p-lower-header__nav-pc {
  display: none;
  @include mq(md) {
    display: block;
    margin-left: auto;
  }
}