@use 'global'as *;

.p-top-contact {
  background: url(../images/bg-contact.jpg)no-repeat center;
  background-size: cover;
  width: 100%;
  @include mq(md) {
    background: url(../images/bg-contact-pc.jpg)no-repeat center;
    background-size: cover;
  }
}

.p-top-contact__inner {
  padding-top: rem(29);
  padding-bottom: rem(29);
  text-align: center;
  @include mq(md) {
    text-align: left;
    padding-top: rem(54);
    padding-bottom: rem(58);
  }
}

.p-top-contact__title {
  font-size: rem(30);
  font-weight: bold;
  text-transform: uppercase;
  color: $colorWhite;
  @include mq(md) {
    font-size: rem(40);
  }
}

.p-top-contact__text {
  margin-top: rem(17);
  font-size: rem(14);
  color: $colorWhite;
  line-height: 2;
  @include mq(md) {
    font-size: rem(16);
    margin-top: 15px;
  }
}

.p-top-contact__button {
  margin-top: rem(35);
  @include mq(md) {
    margin-top: 35px;
  }
}

.p-top-contact__button a {
  max-width: rem(220);
  padding: rem(15) 0;
  letter-spacing: .01em;
  transition: all .3s;
  @include mq(md) {
    max-width: rem(280);
    padding: rem(18.5) 0;
  }
}

.p-top-contact__button .c-button:hover {
  background: #606060;
  opacity: 1;
}