@use 'global'as *;

.p-top-mv__inner {
  display: flex;
  flex-direction: column;
  @include mq(md) {
    position: relative;
    max-width: rem(1366);
    width: 100%;
    margin: 0 auto;
    flex-direction: row-reverse;
  }
}

.p-top-mv__title {
  margin-top: rem(23);
  padding: 0 rem(20);
  font-size: rem(22);
  font-weight: bold;
  line-height: 1.5454;
  order: 2;
  @include mq(md) {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(20%, -52%);
    padding: rem(16) rem(20);
    background-color: #fff;
    z-index: 2;
    font-size: rem(32);
    line-height: 1.6875;
  }
}

.p-top-mv__wrapper {
  position: relative;
  width: 100%;
  z-index: 1;
  @include mq(md) {
    max-width: rem(1100);
    width: 85%;
    margin-right: 0;
  }
}

.p-top-mv__wrapper::before {
  content: "";
  display: block;
  padding-top: 62.564%;
  @include mq(md) {
    padding-top: 62.546%;
  }
}

.p-top-mv__img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}