@use 'global'as *;

.p-footer {
  background: $colorBlack;
  color: $colorWhite;
}

.p-footer__inner {
  max-width: rem(383);
  width: 100%;
  margin: 0 auto;
  padding: rem(40) rem(20) rem(34);
  @include mq(md) {
    display: flex;
    align-items: center;
    max-width: rem(1150);
    padding: rem(40) rem(25) rem(51);
  }
}

.p-footer__nav-lists {
  display: flex;
  justify-content: space-between;
  @include mq(md) {
    justify-content: unset;
    flex-direction: column;
  }
}

.p-footer__nav {
  @include mq(md) {
    order: 2;
    margin-top: rem(13);
  }
}

.p-footer__info {
  margin-left: 0;
  @include mq(md) {
    margin-right: rem(286);
    order: 1;
  }
}

.p-footer__nav-list {
  font-size: rem(16);
  font-weight: bold;
  text-transform: uppercase;
}

.p-footer__nav-list + .p-footer__nav-list {
  @include mq(md) {
    margin-left: 0;
    margin-top: rem(25);
  }
}

.p-footer__logo {
  margin-top: rem(40);
  font-size: rem(24);
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  @include mq(md) {
    margin-top: 0;
    text-align: left;
  }
}

.p-footer__address {
  margin-top: rem(14);
  font-size: rem(14);
  text-align: center;
  line-height: 1.8571;
  text-transform: capitalize;
  @include mq(md) {
    text-align: left;
    margin-top: rem(37);
  }
}

.p-footer__copyright {
  display: block;
  padding: rem(20.2) 0;
  font-size: rem(12);
  font-weight: bold;
  text-align: center;
  background-color: $colorWhite;
  color: $colorBlack;
}