@use 'global'as *;

.c-section-title {
  display: flex;
  align-items: center;
  font-size: rem(30);
  font-weight: bold;
  text-transform: uppercase;
  color: $colorGray;
  @include mq(md) {
    font-size: rem(60);
  }
}

.c-section-title::after {
  content: "";
  display: inline-block;
  border-top: solid 1px $colorTitleBorder;
  width: 100%;
  margin-left: rem(20);
  @include mq(md) {
    max-width: rem(320);
    width: 100%;
  }
}

.c-section-title--reverse {
  @include mq(md) {
    flex-direction: row-reverse;
  }
}

.c-section-title--reverse::after {
  content: "";
  margin-right: rem(20);
}