@use 'global'as *;

.p-breadcrumb {
  display: none;
  @include mq(md) {
    display: block;
  }
}

.p-breadcrumb__inner {
  @include mq(md) {
    max-width: rem(1146);
    width: 100%;
    margin: 0 auto;
    padding: rem(40) rem(20);
  }
}

.p-breadcrumb__items {
  @include mq(md) {
    width: 100%;
    display: flex;
    align-items: center;
  }
}

.p-breadcrumb__item {
  display: flex;
  align-items: center;
  
}

.p-breadcrumb__item img {
  width: rem(16);
  height: rem(14);
}

.p-breadcrumb__item a {
  font-size: rem(14);
  text-transform: uppercase;
}

.p-breadcrumb__item:not(:last-child)::after {
  content: "";
  display: block;
  background: url(../images/icon-arrow-right-gray.svg) no-repeat center;
  background-size: contain;
  width: rem(6.1);
  height: rem(9.4);
  padding-left: rem(20);
  padding-right: rem(20);
}