.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.7;
  }
}

.c-buger-button {
  /* ボタンの大きさ  */
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  margin-left: auto;
  padding: 5px;
  cursor: pointer;
  /* 最前面に */
  z-index: 10;
  background: #333333;
}
@media screen and (min-width: 768px) {
  .c-buger-button {
    width: 5rem;
    height: 5rem;
  }
}

.c-buger-button.open {
  position: fixed;
  top: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .c-buger-button--lower {
    display: none;
  }
}

.c-buger-button__line {
  display: block;
  position: relative;
  /* バーガー線の位置基準として設定 */
  width: 1.25rem;
  /* 線の長さと高さ */
  height: 1px;
  margin: 0 auto;
  background-color: #ffffff;
  /* バーガー線の色 */
  transition: all 0.3s;
}
@media screen and (min-width: 768px) {
  .c-buger-button__line {
    width: 2.5rem;
    height: 0.25rem;
  }
}

.c-buger-button__line::before,
.c-buger-button__line::after {
  content: "";
  position: absolute;
  width: 1.25rem;
  height: 100%;
  background-color: #ffffff;
  left: 0;
  transition: all 0.3s;
}
@media screen and (min-width: 768px) {
  .c-buger-button__line::before,
.c-buger-button__line::after {
    width: 2.5rem;
  }
}

.c-buger-button__line::before {
  /* 上の線の位置 */
  transform: translateY(-0.46875rem);
  transition: all 0.3s;
}
@media screen and (min-width: 768px) {
  .c-buger-button__line::before {
    transform: translateY(-0.9375rem);
  }
}

.c-buger-button__line::after {
  /* 下の線の位置 */
  transform: translateY(0.46875rem);
  transition: all 0.3s;
}
@media screen and (min-width: 768px) {
  .c-buger-button__line::after {
    transform: translateY(0.9375rem);
  }
}

/***** メニューオープン時 *****/
.c-buger-button__line.open {
  background-color: transparent;
  /* 真ん中の線を透明に */
  transition: all 0.3s;
}

.c-buger-button__line.open::before,
.c-buger-button__line.open::after {
  content: "";
  background-color: #ffffff;
  /* 上下の線の色を変える */
}

.c-buger-button__line.open::before {
  transform: rotate(45deg);
  /* 上の線を傾ける */
  transition: all 0.3s;
}

.c-buger-button__line.open::after {
  transform: rotate(-45deg);
  /* 下の線を傾ける */
  transition: all 0.3s;
}

