@use 'global'as *;

.p-form__inner {
  @include mq(md) {
    max-width: rem(568);
    width: 100%;
    margin: 0 auto;
  }
}

.p-form__item {
  @include mq(md) {
    display: flex;
    align-items: center;
  }
}

.p-form__item label {
  font-size: rem(14);
  font-weight: bold;
  @include mq(md) {
    width: 39%;
    font-size: rem(16);
  }
}

.p-form__item--radio {
  @include mq(md) {
    margin-top: rem(28);
  }
}

.p-form__item--radio label {
  @include mq(md) {
    height: rem(50);
  }
}

.p-form__text {
  display: block;
  width: 100%;
  height: rem(50);
  margin-top: rem(21);
  padding: rem(13) rem(20);
  border: solid 1px #CDD6DD;
  @include mq(md) {
    max-width: rem(346);
    margin-top: 0;
  }
}

.p-form__radio {
  display: flex;
  align-items: center;
  font-size: rem(14);
  @include mq(md) {
    font-size: rem(16);
  }
}


.p-form__radio input {
  width: rem(30);
  height: rem(30);
  margin-right: rem(9);
}

::placeholder {
  font-size: rem(16);
  color: #CDD6DD;
}
.p-form__item select {
  position: relative;
  display: block;
  width: 100%;
  height: rem(50);
  margin-top: rem(21);
  padding: rem(13) rem(20);
  border: solid 1px #CDD6DD;
  background: url(../images/icon-pull-down.svg) no-repeat;
  background-position: top 50% right 21px ;
  background-size: rem(9.4) rem(6.1);
  @include mq(md) {
    max-width: rem(346);
    margin-top: 0;
  }
}

.p-form__item textarea {
  width: 100%;
  height: rem(140);
  margin-top: 22px;
  padding: rem(17) rem(20);
  border: solid 1px #CDD6DD;
  @include mq(md) {
    max-width: rem(346);
    margin-top: 0;
  }
}

.p-form__radio:first-of-type {
  margin-top: 24px;
  @include mq(md) {
    margin-top: 0;
  }
}

.p-form__radio:not(:first-of-type) {
  margin-top: 10px;
  @include mq(md) {
    margin-top: 10px;
  }
}

.p-form__button {
  margin-top: rem(40);
}

.p-form__radio-wrapper {
  @include mq(md) {
    display: flex;
    flex-direction: column;
  }
}

.p-form__item--company {
  margin-top: rem(41);
  @include mq(md) {
    margin-top: rem(20);
  }
}

.p-form__item--radio {
  margin-top: rem(41);
  @include mq(md) {
    margin-top: rem(28);
  }
}

.p-form__item--mail {
  margin-top: rem(41);
  @include mq(md) {
    margin-top: rem(43);
  }
}

.p-form__item--tel {
  margin-top: rem(41);
  @include mq(md) {
    margin-top: rem(36);
  }
}

.p-form__item--select {
  margin-top: rem(41);
  @include mq(md) {
    margin-top: rem(40);
  }
}

.p-form__item--textarea {
  margin-top: rem(41);
  @include mq(md) {
    margin-top: rem(40);
  }
}