@use 'global'as *;

.p-media__inner {
  padding-top: rem(55);
  padding-bottom: rem(60);
  @include mq(md) {
    padding-top: rem(100);
    padding-bottom: rem(100);
  }
}

.p-media__inner--company {
  padding-top: rem(28);
  padding-bottom: rem(28);
  @include mq(md) {
    padding-top: rem(101);
    padding-bottom: rem(100);
  }
}

.p-media__body {
  margin-top: rem(20);
  margin-top: 20px;
  @include mq(md) {
    display: flex;
    margin-top: rem(40);
    justify-content: space-between;
  }
}

.p-media__body--reverse {
  @include mq(md) {
    flex-direction: row-reverse;
  }
}

//===================
.p-media__img-wrapper {
  position: relative;
  width: 100%;
  @include mq(md) {
    max-width: rem(600);
    width: 59%;
    margin-right: rem(40);
    margin-left: 0;
  }
}

.p-media__img-wrapper::before {
  content: "";
  display: block;
  padding-top: 60%;
  @include mq(md) {
    padding-top: 60%;
  }
}

.p-media__img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.p-media__img--reverse {
  @include mq(md) {
    margin-left: rem(40);
    margin-right: 0;
  }
}
//===================


.p-media__text-wrapper {
  @include mq(md) {
    width: 41%;
    text-align: left;
  }
}

.p-media__text {
  margin-top: rem(18);
  font-size: rem(16);
  line-height: 2;
  @include mq(md) {
    line-height: 2.25;
    margin-top: 0;
    padding-top: rem(45);
  }
}

.p-media__button {
  margin-top: rem(25);
  @include mq(md) {
    margin-top: rem(33);
  }
}