@use 'global'as *;

.c-button {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: rem(230);
  width: 100%;
  margin: 0 auto;
  padding: rem(16.38) 0;
  font-size: rem(15);
  font-weight: bold;
  letter-spacing: .2em;
  color: $colorWhite;
  background: $colorGray;
  text-transform: uppercase;
  @include mq(md) {
    max-width: rem(280);
    font-size: rem(20);
    padding: rem(18.5) 0;
  }
}

.c-button--left {
  @include mq(md) {
    margin: unset;
  }
}
.c-button--right {
  @include mq(md) {
    margin-right: 0;
  }
}

.c-button::after {
  content: "";
  background-image: url("../images/icon-arrow-right-s.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  width: rem(8);
  height: rem(8);
  margin-left: rem(20);
  @include mq(md) {
    width: rem(9.4);
    height: rem(9.4);
  }
}

.c-button--contact {
  max-width: rem(200);
  padding: rem(14.82) 0;
  font-size: rem(16);
  @include mq(md) {
    max-width: rem(272);
    padding: rem(12.5) 0;
  }
}

.c-button--contact.c-button::after {
  display: none;
}