@use 'global'as *;

.p-nav {
  position: fixed;
  display: block;
  width: 100%;
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh) * 100);
  top: 0;
  left: -100%;
  bottom: 0;
  background: #000;
  text-align: center;
  z-index: 5;
  transition: all .4s;
}

.p-nav.open {
  left: 0;
}

.p-nav__list--logo {
  max-width: rem(241);
  width: 100%;
  margin: 0 auto;
  @include mq(md) {
    max-width: rem(288);
  }
}

.p-nav__lists {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 15%;
  overflow: scroll;
  padding-bottom: 15%;
}

.p-nav__list {
  font-size: rem(28);
  font-weight: bold;
  color: $colorWhite;
  text-transform: uppercase;
  @include mq(md) {
    font-size: rem(34);
  }
}

.p-nav__list a {
  padding: rem(10);
}

.p-nav__list + .p-nav__list {
  margin-top: rem(63);
  margin-top: 7.1vh;
  @include mq(md) {
    margin-top: rem(56);
  }
}

.p-nav--pc {
  display: none;
  @include mq(md) {
    display: block;
  }
}

.p-nav--sp {
  display: block;
  @include mq(md) {
    display: none;
  }
}