@use 'global'as *;

.p-company-info {
  font-size: rem(14);
}

.p-company-info dt {
  width: 30%;
  font-weight: normal;
  color: $colorGray;
  line-height: 2.1428;
  @include mq(md) {
    width: 13.5%;
    font-size: rem(16);
    line-height: 1.875;
  }
}

.p-company-info dd {
  display: inline;
  width: 70%;
  color: $colorBlack;
  line-height: 2;
  @include mq(md) {
    font-size: rem(16);
    line-height: 2.125;
  }
}

.p-company-info__wrapper {
  display: flex;
  padding: rem(15.2) 0;
  border-bottom: solid 1px #CDD6DD;
  @include mq(md) {
    padding: rem(13) 0;
  }
}

.p-company-info__wrapper:first-child {
  border-top: solid 2px #98A6B5;
}

.p-company-info__wrapper span {
  display: none;
  @include mq(md) {
    display: inline-block;
    margin: 0 rem(5);
  }
}